import { Controller } from '@ali/4ever-cangjie';
import { removeInvalidCells, cleanUpColumns } from '../actions';

export default function createOnCut() {
  return function onCut(
    event: React.ClipboardEvent,
    controller: Controller,
    next,
  ) {
    const result = next();
    controller.run('onAction', removeInvalidCells());
    controller.run('onAction', cleanUpColumns());
    return result;
  };
}
